import { Component, Host, h, Prop, Event, Element, EventEmitter } from '@stencil/core';

@Component({
  tag: 'login-form',
  styleUrl: 'login-form.css',
  shadow: false,
})
export class LoginForm {

  @Prop() forgotPasswordUrl: string;
  @Event() loginShouldOccur: EventEmitter;
  @Element() host: HTMLElement;

  login() {
    let form = this.host.querySelector('form');
    if (form.reportValidity()) {
        let inputs = this.host.querySelectorAll('input');
        this.loginShouldOccur.emit({ username: inputs[0].value, password: inputs[1].value });
    }
  }

  render() {
    return (
      <Host>
        <form class="login-form">
          <div class="login-container">
            <input-box fieldName="Username" isRequired={true} fieldType="text"></input-box>
            <input-box fieldName="Email" isRequired={true} fieldType="email"></input-box>
            <button-box buttonName="Login"></button-box>
          </div>
        </form>
      </Host>
    );
  }

}
